const program = {"sessions":[{"intendedAudience":"","endTimeZulu":"2018-10-23T15:00:00Z","level":"beginner","length":"480","format":"workshop","language":"en","sessionId":"fe165087637043a29acf9760bf8cb8a0","abstract":"To attend either buy a TDC ticket with this workshop included for a discounted price or go to https://programutvikling.no/course/application-building-with-asp-net-core-2-1/\n\nIn this new 2-day workshop, you'll be guided by members of the ASP.NET team in building, testing, deploying, and monitoring an application using ASP.NET Core 2.1, on your platform of choice.\n\nLearn the fundamentals and new features in the next major version of ASP.NET Core, including:\n\nGetting started with ASP.NET Core 2.1 in your development environment, either Visual Studio on Windows, of Visual Studio Code on Windows or Mac\nUsing MVC features like Tag Helpers, Model Binding & Validation, and Razor Pages to build HTML forms\nUsing MVC features like API Controllers, Formatters and Routing to build HTTP APIs\nHandling users and authentication using ASP.NET Identity and Security\nManaging data using Entity Framework Core\nRunning background tasks in your application using Hosted Services\nUnit testing your application using xUnit\nDebugging and diagnosing issues in applications using logging and Application Insights\nPublishing applications and running them in Azure\nComputer setup: \nAttendees will need to bring a laptop with the following setup:\n\nWindows: Laptop running Visual Studio 2017 Preview, or Visual Studio Code\nMac: Laptop running Visual Studio Code\nLinux: Laptop running Visual Studio Code","title":"Application building with ASP.NET Core 2.1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-23T07:00:00Z","speakers":[{"twitter":"@jongalloway","pictureId":"6e93af8a68ac4bebb51ab1c2e4764528","pictureUrl":"https://api.trondheimdc.no/public/picture/6e93af8a68ac4bebb51ab1c2e4764528","name":"Jon Galloway","bio":"Jon is the Executive Director of the .NET Foundation and a Program Manager on the .NET team. He’s been developing software professionally since the late 1990’s, and over the years has written several books, popular .NET tutorials and samples, a top speaker on Microsoft Virtual Academy, and worked behind the scenes on large Microsoft keynote demonstrations. He’s helped run the Herding Code podcast for 10 years, and tweets as @jongalloway."}],"startTime":"2018-10-23T09:00","endTime":"2018-10-23T17:00"},{"intendedAudience":"Dette foredraget er for alle, men de som koser seg mest under foredraget er nokk de som interesserer seg for morgendagens teknologier. De som liker å spekulere i fremtidens grensesnitt. De som elsket å åpne illustrert vitenskap hos tannlegen eller bare digger sience fiction. Det skader heller ikke om du er en smule interessert i AR, VR og hologrammer.","endTimeZulu":"2018-10-22T14:00:00Z","level":"beginner","length":30,"format":"presentation","language":"no","sessionId":"ac62e3017c134da4b4f3cd194822353e","abstract":"Visste du at Mark Twain spådde internett allerede i 1889? Eller at VR faktisk er et 83 år gammelt konsept? Science Fiction har spådd mange av teknologiene vi bruker i dag, så da må vel science fiction også kunne spå teknologiene vi kommer til å bruke i morgen? Jeg har gjort et dypdykk inn i science fiction litteratur og film for å prøve å finne fremtidens grensesnitt.\n\nDette er foredraget for deg som liker å spekulere om fremtiden. Vil den se ut som i film? Med transperente skjermer, interaktive hologrammer og superavanserte augmented reality gadgets. Eller kanskje litteraturen har svarene? Med ideer som autonome teknologier, biologiske forbedringer og nevrologiske implantater.\n\nEn ting er sikkert: det er vanskelig å spå fremtiden, men science fiction-forfattere har prøvd å gjøre dette siden sjangerens fødsel, og det er det mye å lære av!","title":"Fra science fiction til virkelighet","room":"Room 5","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T13:30:00Z","speakers":[{"twitter":"@jonaaust","pictureId":"ab8a394d6b024110b3abebf7bc6e86bb","pictureUrl":"https://api.trondheimdc.no/public/picture/ab8a394d6b024110b3abebf7bc6e86bb","name":"Jonatan Austigard","bio":"Jeg designer og programmerer tjenester og produkter. Jeg har en bachelorgrad i grafisk design fra Westerdals School of Communication hvor jeg skrev en bacheloroppgave om grensesnitt i film som er utgangspunktet for denne presentasjonen. I dag jobber jeg som senior designer og fagleder i BEKK Consulting. \n\nJeg spesialiserer meg på idé- og konseptutvikling og liker best å jobbe med tjenester eller produkter som har fokus på innovasjon. Lidenskapen min er å designe løsninger som engasjerer mennesker og skaper reell verdi.\n\nJeg brenner for faget og elsker å lære bort. Jeg har vært gjestelærer på Westerdals, holdt foredrag på NTNU, UIO og Visuelt innen dynamisk design og kreativ programmering. Jeg sitter i dag som komité-nesteleder i Grafill Interaktiv; hvor vi jobber for å fremme digitalt design i Norge."}],"startTime":"2018-10-22T15:30","endTime":"2018-10-22T16:00"},{"intendedAudience":"Experienced Java developers who want to see the new and upcoming features. There will be code on the slides and maybe even a little live-coding.","endTimeZulu":"2018-10-22T10:00:00Z","level":"intermediate","length":45,"format":"presentation","language":"en","sessionId":"4bfe0a6307b14f21a5de143b635abff6","abstract":"Second part.\nWith Java's switch to a six-month release schedule, the community needs to step up its game to stay up to date. Instead of having a year or more to prepare for a new release, it's a mere three months - we need to be better informed about what's going on to see changes coming before they're out.\n\nThis talk:\n\n* explains the details behind the new schedule and how to stay up to date\n* presents Java 9, 10, and 11 language features like `var`\n* shows the future of Java, brought by projects like Valhalla, Amber, and others\n* looks at features of upcoming releases like pattern matching and value types\n\nAfterwards, you will not only know about the immediate future but also how to stay up to date without much effort.","title":"Part 2, Java Next - New Releases, Amber, Valhalla, and More Goodies","room":"Room 5","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T09:15:00Z","speakers":[{"twitter":"@nipafx","pictureUrl":"https://www.gravatar.com/avatar/13eee0ac5d38daea3d6ca6aa6efea5b3?d=mm","name":"Nicolai Parlog","bio":"Nicolai is a thirty year old boy, as the narrator would put it (if he squints), who has found his passion in software development. He constantly reads, thinks, and writes about it, and codes for a living as well as for fun.\n\nNicolai is a freelancing developer, author, and trainer. He wrote The Java Module System[1] with Manning, blogs about software development on codefx.org[2], recently started a YouTube channel[3], and is a long-tail contributor to several open source projects. You can hire him[4] for all kinds of things.\n\n[1]: https://www.manning.com/books/the-java-module-system?a_aid=nipa&a_bid=869915cb\n[2]: http://codefx.org\n[3]: https://youtube.com/c/codefx\n[4]: http://blog.codefx.org/hire-nicolai-parlog/"}],"startTime":"2018-10-22T11:15","endTime":"2018-10-22T12:00"},{"intendedAudience":"Designers, engineers, product managers and people managers who need to think through holistic customer experiences -- from the digital to the physical. ","endTimeZulu":"2018-10-22T09:00:00Z","level":"intermediate","length":"45","format":"presentation","language":"en","sessionId":"3a55bc82379942c6941c0570ab61614c","abstract":"Where does a product start and a service end? Does it matter? The rise of digital has brought product and service together. The bad news is that our products are now more complex than ever. The good news is that a set of methods for dealing with complexity has emerged. Through a series of detailed case studies, we’ll show how teams at large companies and startups are combining modern lean and agile tactics with service design methods to create and deliver new digital products—faster, with less risk, and with better customer outcomes than ever before.\n","title":"Sense and Respond - Products and Services in the Age of Complexity","room":"Room 2","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T08:15:00Z","speakers":[{"twitter":"@jboogie","pictureUrl":"https://www.gravatar.com/avatar/311da4818e5b5f17d1d3774b1e034070?d=mm","name":"Jeff Gothelf","bio":"Jeff is the co-author of Lean UX and Sense & Respond. After a long career as an interaction designer Jeff now works with large companies to build customer-centric, collaborative, agile organizations. "},{"twitter":"@jseiden","pictureId":"d6b8c683660c42098a8981d376c1efdc","pictureUrl":"https://api.trondheimdc.no/public/picture/d6b8c683660c42098a8981d376c1efdc","name":"Josh Seiden","bio":"Josh Seiden is a designer, author and coach who works with teams to create great products and services. He helps teams solve real problems for customers and create value for businesses.   He also works as a coach to help teams improve the way they work, build more effective collaborations, and enjoy the journey of product development.   He's a co-founder of Sense & Respond Press, a micro-publisher that creates short, focused books on innovation, digital transformation, and product management.   And he's the co-author of two books: \"Sense & Respond\" (from Harvard Business Publishing) and  \"Lean UX\" (from O'Reilly).  "}],"startTime":"2018-10-22T10:15","endTime":"2018-10-22T11:00"},{"intendedAudience":"Experienced Java developers who want to see the new and upcoming features. There will be code on the slides and maybe even a little live-coding.","endTimeZulu":"2018-10-22T09:00:00Z","level":"intermediate","length":45,"format":"presentation","language":"en","sessionId":"1e533094b0bb4db9a84dde98f8a65058","abstract":"With Java's switch to a six-month release schedule, the community needs to step up its game to stay up to date. Instead of having a year or more to prepare for a new release, it's a mere three months - we need to be better informed about what's going on to see changes coming before they're out.\n\nThis talk:\n\n* explains the details behind the new schedule and how to stay up to date\n* presents Java 9, 10, and 11 language features like `var`\n* shows the future of Java, brought by projects like Valhalla, Amber, and others\n* looks at features of upcoming releases like pattern matching and value types\n\nAfterwards, you will not only know about the immediate future but also how to stay up to date without much effort.\n","title":"Java Next - New Releases, Amber, Valhalla, and More Goodies","room":"Room 5","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T08:15:00Z","speakers":[{"twitter":"@nipafx","pictureUrl":"https://www.gravatar.com/avatar/13eee0ac5d38daea3d6ca6aa6efea5b3?d=mm","name":"Nicolai Parlog","bio":"Nicolai is a thirty year old boy, as the narrator would put it (if he squints), who has found his passion in software development. He constantly reads, thinks, and writes about it, and codes for a living as well as for fun.\n\nNicolai is a freelancing developer, author, and trainer. He wrote The Java Module System[1] with Manning, blogs about software development on codefx.org[2], recently started a YouTube channel[3], and is a long-tail contributor to several open source projects. You can hire him[4] for all kinds of things.\n\n[1]: https://www.manning.com/books/the-java-module-system?a_aid=nipa&a_bid=869915cb\n[2]: http://codefx.org\n[3]: https://youtube.com/c/codefx\n[4]: http://blog.codefx.org/hire-nicolai-parlog/\n"}],"startTime":"2018-10-22T10:15","endTime":"2018-10-22T11:00"},{"intendedAudience":"You'll learn how Docker and Kubernetes work, and how to best take advantage of these new technologies. Throughout the course we'll take you through a variety of hands-on exercises designed to help you quickly grow from a beginner to a seasoned user.","endTimeZulu":"2018-10-23T15:00:00Z","level":"beginner","length":"480","format":"workshop","language":"en","sessionId":"3eca0f4eef144f92af06bd2c690b83ec","abstract":"This course takes you through building, integrating and running containers.\n\nTo attend either buy a ticket to TDC that includes discounted attendance for this workshop, or use https://programutvikling.no/course/docker-and-kubernetes-for-developers/ \n\nExercises include:\n\nInstalling Docker\nCreating your first Docker container\nBuilding Docker images\nStoring and retrieving Docker images from registries such as Docker Hub\nBuilding containers from images\nDocker volumes\nContainer security\nUsing Docker for sandboxing and testing\nDeploying applications with Docker\nOrchestration by docker-compose\nIntroduction to kubernetes concepts\nDeploying, upgrading and monitoring your cloud native applications on kubernetes\nBy the end of the course you will be familiar with the \"why\" of Docker. You will also be able to perform the basic tasks needed to get started with Docker and integrate it into your development process.\n\nComputer setup: \nAttendees will need to bring a laptop with an SSH Client installed.","title":"Docker and Kubernetes for Developers","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-23T07:00:00Z","speakers":[{"twitter":"@meekrosoft","pictureId":"842b14db89794d3ab254e5e0d3b3f4fa","pictureUrl":"https://api.trondheimdc.no/public/picture/842b14db89794d3ab254e5e0d3b3f4fa","name":"Mike Long","bio":"Mike is a Partner at Praqma, a Continuous Delivery and Devops consulting company based in Scandinavia. He has extensive experience delivering software in various cultures and industries. He helps organize several community events and conferences, including CoDe Academy which teaches Continuous Delivery to university students. Mike is a trustee on the cyber-dojo foundation."}],"startTime":"2018-10-23T09:00","endTime":"2018-10-23T17:00"},{"intendedAudience":"Mitt mål er å lage et foredrag som har noe for alle og ikke krever en spesiell kompetanse for å få glede av det, men jeg ser for meg at dette blir spesielt treffende for \n\n1) utviklere med erfaring med React, som synes det høres spennende ut å lære mer om React Native \n2) utviklere som jobber med native mobilteknologi, og er nysgjerrige på kryssplattformalternativer \n3) alle som liker en god historie! Jeg elsker storytelling, og prøver aktivt å trene på å bli en god forteller.\n\n","endTimeZulu":"2018-10-22T14:45:00Z","level":"beginner","length":"30","format":"presentation","language":"no","sessionId":"d74c8726bd9b447f996b6d552073c555","abstract":"Høsten 2017 gjennomførte et team hos NSB det største tekniske løftet noensinne i statsbanenes mobilapper. Måten billetter kjøpes, lagres og ikke minst fremvises til kontrollør blitt totalt omforvandlet. Stikkord: React Native. \n\nUnderveis har vi lært masse, gjort oss både gode og dårlige erfaringer rundt teknologi, prosess og organisering – rett og slett hvordan vi gjør digital produktutvikling. Resultatet har siden funnet veien til flere hundre tusen mobiltelefoner rundt omkring i det ganske land, fra varme stuer til kalde perronger. \n\nI dette foredraget lærer du litt om den røde appen på telefonen din, om kryssplatform mobilutvikling med React Native, og du følger teamets reise fra idé til produksjon for monsterfeaturen \"skybillett\".\n\n","title":"Apputvikling på stereoider med React Native - en fortelling om NSBs billettapp","room":"Room 4","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T14:15:00Z","speakers":[{"twitter":"@ewndl","pictureId":"797c413359d6416db89005e288bf5481","pictureUrl":"https://api.trondheimdc.no/public/picture/797c413359d6416db89005e288bf5481","name":"Erik Wendel","bio":"Koder, pratmaker og fagsjef hos Bekk.\nOpptatt av gode kode, godt samarbeid og gode digitale produkter.\n\nFor tiden engasjert hos de norske statsbaner med å levere en best mulig appopplevelse til hundretusener av togreisende.\n\nGrunnlegger av Oslo Elm Meetup og Oslo Elm Day."}],"startTime":"2018-10-22T16:15","endTime":"2018-10-22T16:45"},{"intendedAudience":"Passer for alle og enhver. Uformelt og tidvis interessant.","endTimeZulu":"2018-10-22T10:45:00Z","level":"beginner","length":"30","format":"presentation","language":"no","sessionId":"396907a4d5d24439a032501db29c903e","abstract":"BartJS Podcast er en \"alt av JavaScript\"-podcast sentrert i Trondheim. Hver måned har vi med en gjest og har en uformell samtale om et tema som berører JavaScript.\n\nI fjor hadde BartJS Podcast sin aller første liveinnspilling på Trondheim Developer Conference, hvor mange mennesker møtte opp i lunsjen for å høre på engasjerte diskusjoner rundt JavaScript og fag. Vi syns det fungerte så bra at vi vil gjennomføre det igjen! Vi har lest igjennom artikler av et egenkomponert nyhetsbrev, og snakker hva vi likte og ikke. Her får du muligheten til å få med deg ulike perspektiver, høre diskusjoner og lære mye om JavaScript. Det kan også bli muligheter for å stille spørsmål.","title":"BartJS Podcast Live #2","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T10:15:00Z","speakers":[{"twitter":"@mikaelbrevik","pictureUrl":"https://www.gravatar.com/avatar/c1ad8925b70df4f6665315865511eafc?d=mm","name":"Mikael Brevik","bio":"Mikael er en aktiv fagperson som er lidenskapelig opptatt av programmering, open source-tankegang og popkultur. I tillegg til å hoste to programmeringspodcaster, gir han ut ukentlige programmeringsvideoer på YouTube og er aktivt engasjert med å bygge fagmiljøet til JavaScript i Trondheim. Mikael har organisert BartJS Meetup i nærmere 5 år."},{"twitter":"@christianalfoni","pictureUrl":"https://www.gravatar.com/avatar/60a5ce418250e26b42baef8ef7314f39?d=mm","name":"Christian Alfoni","bio":"Christian fant kjærligheten en sen høstdag for over 8 år siden og spenningen i forholdet er like potent i dag som den gang, om ikke mer. JavaScript og Christian feirer snart 9 års jubileum med bidrag i profesjonelle prosjekter, open source og privat."},{"twitter":"@larrifax","pictureUrl":"https://www.gravatar.com/avatar/0149c56e84a4b03fef19c39ed27cc00d?d=mm","name":" Kristian Tryggestad","bio":"Kristian er en full-stack utvikler som trives aller best når han får jobbe med Javascript, HTML og CSS. Han er interessert i det aller meste som kan knyttes mot utvikling av web-applikasjoner, men er spesielt glad i nyvinninger som driver weben fremover og samtidig gjør livet lettere for oss utviklere."},{"twitter":"@krakels","pictureUrl":"https://www.gravatar.com/avatar/fd5606a96714d8a21d010621dea143e6?d=mm","name":" Marius Krakeli","bio":"Marius er en programmerer med forkjærlighet for behagelige brukeropplevelser. Han lar seg fascinere av JavaScript og er produsent og fast panelist på JavaScript-podcasten BartJS Podcast. Når han trenger et avbrekk fra programmeringsrelaterte aktiviteter, er han veldig underholdt av dataspill og videoproduksjon."}],"startTime":"2018-10-22T12:15","endTime":"2018-10-22T12:45"},{"intendedAudience":"Everyone","endTimeZulu":"2018-10-22T11:45:00Z","level":"beginner","length":45,"format":"presentation","language":"en","sessionId":"a2ef5708c84648728471f169e60953dd","abstract":"TDB","title":"Post lunch keynote","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T11:00:00Z","speakers":[{"twitter":"@_JulienS","pictureId":"8931de4c7e794f41914fdfe671c2b790","pictureUrl":"https://api.trondheimdc.no/public/picture/8931de4c7e794f41914fdfe671c2b790","name":"Julien S. Bourrelle","bio":"Scandinavian-loving rocket scientist. Author of The Social Guidebook NO/SE/DK. Keynote speaker."}],"startTime":"2018-10-22T13:00","endTime":"2018-10-22T13:45"},{"intendedAudience":"","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","level":"intermediate","speakers":[{"twitter":"","pictureId":"53ba9d7a48e14175b79b44d91cceaf0f","pictureUrl":"https://api.trondheimdc.no/public/picture/53ba9d7a48e14175b79b44d91cceaf0f","name":"Hubert Matthews","bio":"Hubert Matthews has been programming in C++ for over 15 years and he has been teaching it for over 10 years. He also works as a systems architect and software consultant. Hubert has been a member of the UK BSI standards committee for C++. Hubert Matthews is a freelance consultant specialising in system architecture and design as well as training programmers in C++, UML and Java. His clients range from large companies such as DHL and Orange to small companies and startups. He also is technical director or advisor to a number of startup companies. Hubert lives in Oxford and in his abundant spare time he likes to pretend that he coaches rowing, dances salsa, dabbles with martial arts and drives too fast."}],"length":"480","format":"workshop","language":"en","sessionId":"60d1bb9774204aa4810035779a41732f","abstract":"To attend either buy a TDC ticket with this workshop included for a discounted price or go to https://programutvikling.no/course/c-17/.\n\nThis 2-day course covers all the major topics in C++17. It is designed to give experienced C++ programmers who know C++11/14 a solid foundation in all aspects of the language and library innovations in the latest version of the C++ Standard.\n\nThe course is practically focused with lots of emphasis on hands-on labs and practical exercises. Please note that delegates will need the latest versions of current compilers in order to use C++17 features, and support for language and library features is still only partially complete. Recommended minimum compiler versions are gcc 7, clang 5 or Visual Studio 2017 15.8. \n\nA detailed list of the topics covered is show below:\n\nC++17 language features\n\n- structured bindings\n- copy elision\n- fold expressions\n- if/switch initializers\n- constexpr if\n- deduction guides\n- inline variables\n- nested namespaces\n\nC++17 miscellaneous features\n\nRemovals and deprecations\n\nC++17 library features\n\n- optional, any, variant\n- byte, string_view\n- node_handle\n- invoke, apply\n- filesystem\n- void_t\n\nDetection idiom\n\nC++17 concurrency\n\n- parallel algorithms\n- new algorithms\n\nComputer setup:\nAttendees will need to bring a laptop with minimum compiler versions of gcc 7, clang 5 or Visual Studio 2017 15.8.","title":"C++17"},{"intendedAudience":"The talk is suiting for beginners, but also for people that are looking for a hands-on example of how CSS Custom properties can actually be put to use, and for people looking for inspiration of how to see accessibility as inspiration rather than a hurdle. There will be some code in the slides","endTimeZulu":"2018-10-22T13:45:00Z","level":"beginner","length":45,"format":"presentation","language":"en","sessionId":"89c94c1f856b4bb489ca1d60e620f31d","abstract":"In Norway, all websites are required by law to be accessible. That has inspired us rather than constrained us when building our video consultation service. How could we make it easy for users to pick their own brand color, and still make sure they had sufficient contrast? Combining CSS Custom Properties with a sprinkle of JavaScript we were able to create a color palette and components that also meet the WCAG criteria (and learned a lot on the way).","title":"Accessible by Law! Generating Colors with JS and CSS Custom Properties","room":"Room 2","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T13:00:00Z","speakers":[{"twitter":"@ingvildindrebo","pictureId":"386b09c8bd714636902598d32a74facb","pictureUrl":"https://api.trondheimdc.no/public/picture/386b09c8bd714636902598d32a74facb","name":"Ingvild Indrebø","bio":"Ingvild is a Senior Developer at the video conferencing startup Confrere. She fell in love with coding after realising code makes it possible for her to to do something good for not just one person at the time - but for everyone that might be using what she built. Before joining Confrere, she worked 4 years at BEKK Consulting, specializing in frontend. A fun fact: even after working in a video conferencing startup, she still finds video conversations slightly awkward. "}],"startTime":"2018-10-22T15:00","endTime":"2018-10-22T15:45"},{"intendedAudience":"Både driftere og utviklere passer godt. Det vil blir slides og live kode. ","endTimeZulu":"2018-10-22T13:15:00Z","level":"intermediate","length":30,"format":"presentation","language":"no","sessionId":"1974766bdd254439b81f0610762e93a1","abstract":"Infrastruktur som kode og konfigurasjonshåndtering har blitt allemanseie, og er ofte første verktøy som plukkes opp av kassa i en bedrifts reise mot DevOps-kulturen. Men hva gjør man om driftsavdelingen er tradisjonell og synes Chef blir for utviklernært og vanskelig? Eller består organisasjonen av et overtall utviklere som vil pushe features, ikke skrive Puppet moduler eller bry seg om servere? \nKanskje består serverparken både av Windows Server og Linux, og tilstanden er et sarlig kaos? Frykt ikke, Ansible er her for å hjelpe. Med sin agentløse kommunikasjonsmodell, lette syntaks og høye abstraksjonsnivå har Ansible blitt en industrifavoritt for både utviklere og driftere som ønsker å automatisere, provisionere, og konfigurere servere og infrastruktur, samt rulle ut programvare på en enkel og trygg måte. I dette foredraget skal vi se hvordan vi gikk fra manuelle rutiner til automatisert trygget, både på Windows og Linux takket være Ansible.","title":"Ansible: Configuration Management for Windows og Linux i skjønn forening","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:45:00Z","speakers":[{"twitter":"@amostii","pictureId":"c2396bace72643c086bed5ccea433f86","pictureUrl":"https://api.trondheimdc.no/public/picture/c2396bace72643c086bed5ccea433f86","name":"Andreas Mosti","bio":"Andreas er back-end utvikler og infrastrukturansvarlig. Han har Open Source i hjertet og DevOps-kulturen pumpende gjennom årene. Kan det scriptes har Andreas sannsynligvis allerede gjort det."}],"startTime":"2018-10-22T14:45","endTime":"2018-10-22T15:15"},{"intendedAudience":"All levels welcome. No code.","endTimeZulu":"2018-10-22T15:50:00Z","level":"beginner","length":50,"format":"presentation","language":"en","sessionId":"f520e251fabc428da2ba290bae01bc32","abstract":"Computers surround us in our daily lives, but the most powerful ideas of computing go beyond the sleek silver containers and glowing boxes. What are these ideas? And how do we prepare our kids for a world where more and more of the problems around us look like computers? \n\nTo find out, we'll travel back in time: from a jacquard loom maker with his punch cards, an inventor obsessed with cogs and steam, an electrical engineer who combined laws of electrical circuits with eccentric English mathematical logic and countless other philosophers, material scientists, artists, dreamers and tinkerers. Technology is about humans, and big their ideas. \n\nLinda Liukas will talk about how we can move towards a more humane tech industry, and identifies what it means to approach technology from the unique perspective.\n\nThrough practical examples, this keynote explores modern interpretations of the intersection between humanities and technology.\n\n","title":"One Hundred Languages","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T15:00:00Z","speakers":[{"twitter":"@lindaliukas","pictureUrl":"https://www.gravatar.com/avatar/3a47ac3f1fa7b6454f4c2c7d13fdcb2d?d=mm","name":"Linda LIukas","bio":"Linda is the author and illustrator of Hello Ruby, a children’s picture book about the whimsical world of computers translated to over 22 languages, as well as the founder of Rails Girls, a global movement to teach young women programming in over 270 cities. She loves Muji, Zelda Fitzgerald, software and sparkly things. Currently she is a TED Resident in New York. "}],"startTime":"2018-10-22T17:00","endTime":"2018-10-22T17:50"},{"intendedAudience":"Architects, developers, testers, product owners\nNo code/code in slides\n","endTimeZulu":"2018-10-22T12:30:00Z","level":"intermediate","length":"30","format":"presentation","language":"en","sessionId":"5bee20002935421a8558d959dc660980","abstract":"Lindbak shares their practical experiences with migration, synchronization and security challenges in the journey from on-premise to the cloud. Lindbak is developing and implementing solutions for sales, loyalty and store management to leading retailers in the Nordic market, based on Microsoft technology and Azure.","title":"From on-premise to multi tenant cloud - 3 years in \"hell\"","room":"Room 4","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:00:00Z","speakers":[{"twitter":"","pictureId":"29dbc1be267e4ded93fae870c883aeb5","pictureUrl":"https://api.trondheimdc.no/public/picture/29dbc1be267e4ded93fae870c883aeb5","name":"Trond Klevstuen","bio":"Chief Technology Innovation Officer, responsible for product- and technology strategy at Lindbak since 2006, always trying to keep the balance between quality and stability and the fun of implementing the latest technology"},{"twitter":"","pictureId":"da529aa7dc7d4a0185054ef31408451d","pictureUrl":"https://api.trondheimdc.no/public/picture/da529aa7dc7d4a0185054ef31408451d","name":"Stein Morten Rustad","bio":"Chief Architect, omni channel sales guru, loves images of cats and drives of course a Tesla..."}],"startTime":"2018-10-22T14:00","endTime":"2018-10-22T14:30"},{"intendedAudience":"Utviklere, arkitekter, produkteiere, prosjektledere og andre som jobber med programvare i komplekse domener av en viss grad. Fokus er på metoder for læring og domenekunnskap og ikke på kode eller teknologi.","endTimeZulu":"2018-10-22T14:00:00Z","level":"intermediate","length":30,"format":"presentation","language":"no","sessionId":"47cf387fea6b4bf19f63f945c5453c25","abstract":"Programvareutviklere er som regel løsningsorienterte. Det anses ofte som en styrke at utviklere er raske til å foreslå tekniske løsninger til vanskelige problemstillinger. Denne evnen til å tenke løsning først kan imidlertid skape utfordringer i forretningsområder der selve forretningsregler har innebygd en høy grad av kompleksitet. Der kan det være mye viktigere å finne riktige problemer som må løses, for deretter å velge riktig teknologi.\n\nDomain Driven Design (DDD) er en tilnærming der man tenker domene først under utvikling av programvare. I dette foredraget presenteres eksempler på hvordan team som jobber med programvare i helse-domenet har anvendt DDD-teknikk kalt Event Storming til å eksponere kompleksitet i forretningsregler. Event Storming kombinert med et viktig strategisk DDD-pattern Bounded Contexts viser at kompleksiteten kan reduseres ved å dele opp applikasjonen man utvikler i ulike kontekster som tilsvarer mest mulig naturlige subdomener i forretningsområdet. ","title":"Strategies to Learn Complex Domains - Experiences from Developing Enterprise Software for Healthcare","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T13:30:00Z","speakers":[{"twitter":"@mufridk","pictureId":"8c42fd8ad2d04f9a8201e38cea4bd3a4","pictureUrl":"https://api.trondheimdc.no/public/picture/8c42fd8ad2d04f9a8201e38cea4bd3a4","name":"Mufrid Krilic","bio":"Erfaring som utvikler i 17 år, i komplekse domener, telekom- og helseindustrien.\nHar jobbet aktivt med Domain Driven Design (DDD) i siste 4 år, både på strategisk og taktisk nivå. Er veldig opptatt av å øke bevissthet rundt forretningsforståelse hos utviklere. Denne iveren har ført til at jeg ble kjent med nye DDD teknikker for læring - Event Storming, Domain Storytelling og Impact Mapping for å nevne noen. Jobber for at teamene må investere i å forstå forretningsprosesser og modellere programvaren på bakgrunn av den kunnskapen. Bor og jobber i Bodø, DIPS AS, ledende programvarehus for helsesektoren i Norge."}],"startTime":"2018-10-22T15:30","endTime":"2018-10-22T16:00"},{"intendedAudience":"This is best suited for intermediate programmers, though it can be appreciated by anybody with an interest in software development. While we present the topic in the context of Python, mutation testing as a technique is largely language agnostic, so developers from any background might benefit from it.\n\nThere will be a small amount of code in the slides, but it's generally not necessary to understand the code in order to appreciate the points that are being made.\n\nIf time permits there will be a quick demo at the end which involves a small amount of coding.","endTimeZulu":"2018-10-22T10:00:00Z","level":"intermediate","length":45,"format":"presentation","language":"en","sessionId":"0d05580ce2884c7a8ac64a92ee9a2b18","abstract":"Mutation testing is a technique for systematically mutating source code in order to validate test suites. It operates by making small changes to a program’s source code and then running a test suite; if the test suite ever succeeds on mutated code then a flag is raised. The goal is to check that a system’s test suite is sufficiently powerful to detect a large class of functionality-affecting changes, thereby helping ensure that the system functions as expected. While not in widespread use, mutation testing is a fascinating topic with great potential that has valuable lessons for the broader software development community. \n\nIn this talk we’ll look at Cosmic Ray, an open-source mutation testing tool for Python. Mutation testing presents some difficult and fascinating challenges - both conceptually and from an implementation point of view - so we’ll look at how Cosmic Ray addresses (or plans to address) these complexities. While some of these details will necessarily be Python-specific, there are lessons in Cosmic Ray for the development of mutation in any language. \n\nWhile mutation testing is still a rather exotic testing technique, it can produce genuinely useful and surprising results. To show this, we’ll look at a number of cases where Cosmic Ray has helped developers improve their test suites and tighten up their implementations.","title":"Mutation Testing in Python with Cosmic Ray","room":"Room 4","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T09:15:00Z","speakers":[{"twitter":"@austin_bingham","pictureUrl":"https://www.gravatar.com/avatar/b72b29740643f1e634d4aca1e6f079ab?d=mm","name":"Austin Bingham","bio":"Austin is a founding director of Sixty North, a software consulting, training, and application development company. A native of Texas, in 2008 Austin moved to Stavanger, Norway where he helped develop industry-leading oil reservoir modeling software in C++ and Python. Prior to that he worked at National Instruments developing LabVIEW, at Applied Research Labs (Univ. of Texas at Austin) developing sonar systems for the U.S. Navy, and at a number of telecommunications companies. He is an experienced presenter, teacher, and author, as well as an active member of the open source community. Austin is the founder of Stavanger Software Developers, one of the largest and most active social software groups in the Stavanger region. Austin holds a Master of Science in Computer Engineering from the University of Texas at Austin. "}],"startTime":"2018-10-22T11:15","endTime":"2018-10-22T12:00"},{"intendedAudience":"Designers, engineers, product managers and people managers who need to think through holistic customer experiences -- from the digital to the physical. ","endTimeZulu":"2018-10-22T10:00:00Z","level":"beginner","length":"45","format":"presentation","language":"en","sessionId":"65ae4155d00d49b6aa7bf9ff03cbd15c","abstract":"Where does a product start and a service end? Does it matter? The rise of digital has brought product and service together. The bad news is that our products are now more complex than ever. The good news is that a set of methods for dealing with complexity has emerged. Through a series of detailed case studies, we’ll show how teams at large companies and startups are combining modern lean and agile tactics with service design methods to create and deliver new digital products—faster, with less risk, and with better customer outcomes than ever before.","title":"Part 2, Sense and Respond - Products and Services in the Age of Complexity","room":"Room 2","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T09:15:00Z","speakers":[{"twitter":"@jboogie","pictureUrl":"https://www.gravatar.com/avatar/311da4818e5b5f17d1d3774b1e034070?d=mm","name":"Jeff Gothelf","bio":"Jeff is the co-author of Lean UX and Sense & Respond. After a long career as an interaction designer Jeff now works with large companies to build customer-centric, collaborative, agile organizations. "},{"twitter":"@jseiden","pictureId":"e6209d670b5d4d119cdca905a4cc34d1","pictureUrl":"https://api.trondheimdc.no/public/picture/e6209d670b5d4d119cdca905a4cc34d1","name":"Josh Seiden","bio":"Josh Seiden is a designer, author and coach who works with teams to create great products and services. He helps teams solve real problems for customers and create value for businesses.   He also works as a coach to help teams improve the way they work, build more effective collaborations, and enjoy the journey of product development.   He's a co-founder of Sense & Respond Press, a micro-publisher that creates short, focused books on innovation, digital transformation, and product management.   And he's the co-author of two books: \"Sense & Respond\" (from Harvard Business Publishing) and  \"Lean UX\" (from O'Reilly).  "}],"startTime":"2018-10-22T11:15","endTime":"2018-10-22T12:00"},{"intendedAudience":"Developers interessted in writing tests that are more robust in the face of changes. ","endTimeZulu":"2018-10-22T14:45:00Z","level":"beginner","length":"45","format":"presentation","language":"en","sessionId":"b8a4fa58e1ec4a77a0f961ac63d0a77c","abstract":"In the seminal book Refactoring, Martin Fowler writes that the prerequisite for refactoring is a good test suite. Yet, many developers experience the opposite. When they make even the slightest changes to code, tests break.\n\nFunctional programming can teach us much about writing more sustainable code, including tests that are more robust in the face of changes. In this talk, I’ll share a bit of what I’ve learned from functional programming, and how you can use it to make C# code more testable, and thereby the tests less fragile.","title":"From poor to pure unit tests","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T14:00:00Z","speakers":[{"twitter":"@ploeh","pictureUrl":"https://www.gravatar.com/avatar/33526616d4a11f1630d4939b769cfbf9?d=mm","name":"Mark Seemann","bio":"Mark Seemann helps programmers make code easier to maintain. His professional interests include functional programming, object–oriented development, software architecture, as well as software development in general. Apart from writing a book about Dependency Injection he has also created several Pluralsight courses, and written numerous articles and blog posts about programming. Originally poised to become a rock star or (failing that) graphic novelist (in the European tradition) he one day found himself with insufficient talent for either, a master's degree in Economics, and a desire for working with computers. He has been doing the latter intermittently since 1995. When not working with software or spending time with his family, Mark enjoys reading, listening to and playing music, as well as preparing or consuming gourmet food and wine."}],"startTime":"2018-10-22T16:00","endTime":"2018-10-22T16:45"},{"intendedAudience":"For alle som ønsker en dypere forståelse for hvordan man selv og de rundt deg kan motiveres og trives på jobb. Hvordan man kontinuerlig forbedrer seg effektivt, både som team og enkeltperson.\n","endTimeZulu":"2018-10-22T13:15:00Z","level":"intermediate","length":30,"format":"presentation","language":"no","sessionId":"459c2e6f9a5942d38e582b557f4e6be5","abstract":"Hvordan kan vi effektivisere prosessen fra et nyoppstartet team til et høy-ytelses team?\n\nHvordan bør vi fasitlitere bedrifter, ansatte og team, slik at man virkelig trives og er motivert på jobb?\n\nHvorfor bør utviklere, ledere og ansatte generelt bry seg om kontinuerlig forbedring?\n\nI denne presentasjon tas det utgangspunkt i to prinsipper som er svært viktige for teamutvikling og det diskuteres hvorfor de er så viktige.\n","title":"Viktigheten av teamutvikling og hvordan gjøre det så effektivt som mulig","room":"Room 4","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:45:00Z","speakers":[{"twitter":"","pictureUrl":"https://www.gravatar.com/avatar/b978433a58a86a58bedab9580b0ee646?d=mm","name":"Robin Sporstøl Koien","bio":"Robin leder teamet bak Evetro, en startup som utvikler et system for kontinuerlig forbedring og retrospektiver. Han har faglig utdannelse fra NTNU innen IKT, samhandling, programvareutvikling, prosjektledelse og entreprenørskap. Robin brenner for utvikling og forbedringsteknikker. Han ønsker nå å dele sin kunnskap og idéer innen teamutvikling, kontinuerlig forbedring og retrospektiver. Han har stor tro på at dette kan tas til et nytt nivå."}],"startTime":"2018-10-22T14:45","endTime":"2018-10-22T15:15"},{"intendedAudience":"Developers, Architects - no code, only principles  - architecture, diagrams - lots of demos + physical models and hardware.","endTimeZulu":"2018-10-22T12:30:00Z","level":"intermediate","length":30,"format":"presentation","language":"en","sessionId":"29bd4eb8e0264bdda0e87c80254f0989","abstract":"With the shipping industry facing new regulations in 2020 on fuel, causing them to have to change fuel and increase expenses with 60 billion dollars - you have to start thinking about how how you can become more efficient on fuel. Shipping is also an industry with great potential for smarter solutions. In this talk you'll get a glimpse at how we believe we've cracked the code and in the making created a maritime reference architecture for both hardware and software - both on-board and connectivity to the cloud. In addition, how do you continuously deliver software in these environments. How can these things also be visualized. In a joint effort between Cisco, Wilhelmsen, Microsoft and Dolittle - we've manage to prove this all end-to-end. Expect this to be demo heavy with physical models, hardware, machine learning, IoT, Kubernetes, 3D - Digital Twin, Hololens.","title":"How to save 60 billion dollar annually","room":"Room 5","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:00:00Z","speakers":[{"twitter":"@einari","pictureUrl":"https://www.gravatar.com/avatar/f26e5f9ec2dcdcb883a95fa1f30a0ddc?d=mm","name":"Einar Ingebrigtsen","bio":"Passionate fullstack developer and architect with almost 25 years of experience from everything from games development on consoles to large distributed systems running in the cloud."},{"twitter":"","pictureUrl":"https://www.gravatar.com/avatar/34411a1a317e7e48c44ed0557b53ee9a?d=mm","name":"Frank Tuhus","bio":""}],"startTime":"2018-10-22T14:00","endTime":"2018-10-22T14:30"},{"intendedAudience":"Det blir lite kode her, men jeg tror dette er et viktig tema både for utviklere og arkitekter. Foredraget sikter seg inn på de som har noen års erfaring og forventer at man forstår prinsippene bak temaer som kryptografi og autentisering/autorisasjon.","endTimeZulu":"2018-10-22T14:45:00Z","level":"intermediate","length":"30","format":"presentation","language":"no","sessionId":"1b9e9741b5954773bd493303f9168bd8","abstract":"Nå som GDPR har blitt lovfestet så er det ikke bare flaut med dårlig sikkerhet, det er farlig for butikken din!\nI dette foredraget vil jeg gå gjennom en del prinsipper for hvordan du bygger applikasjoner der risikoen for å miste data er minimal. For å gjøre dette skikkelig må applikasjonen ha riktig arkitektur, den må håndtere kryptografi og tilgangsstyring på en god måte og du må ha gjort fornuftige avveininger mellom risiko og funksjonalitet. \nForedraget er basert på mine erfaringer fra systemutvikling innenfor helsevesenet og de valgene vi har måttet ta der. ","title":"Plutselig var det farlig å miste data","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T14:15:00Z","speakers":[{"twitter":"@runegri","pictureId":"272aebaec347447ba7f5380edf326706","pictureUrl":"https://api.trondheimdc.no/public/picture/272aebaec347447ba7f5380edf326706","name":"Rune Andreas Grimstad","bio":"Rune har programmert siden barndommen og har ikke tenkt å slutte med det! For tiden jobber han mye med sikkerhet og lager systemer som skal håndtere sensitive helseopplysninger."}],"startTime":"2018-10-22T16:15","endTime":"2018-10-22T16:45"},{"intendedAudience":"Audience: developers, practical examples showing how security testing benefits. \nManagement: show how a workflow can be organized to improve the security standing of the company and get insights through metrics, needed for organizational learning and long-term improvement. \n\nCode level: some code in slides. ","endTimeZulu":"2018-10-22T14:00:00Z","level":"intermediate","length":30,"format":"presentation","language":"no","sessionId":"e2e36dd85e904cf2af9a220725501a12","abstract":"Goals: give developers insight into how automated testing can help them write more secure code without adding a lot of overhead. Show managers one approach to organize development processes for improved security.\n\nContents (talk will be given in Norwegian): \n---------------------\nTesting må til for å få til en god sikkerhetskultur blant utviklere! Sikkerhetstester må ikke tilføre en stor arbeidsbyrde, gi relevant informasjon raske forbedringer i applikasjonsmiljøet. For å få til dette, må applikasjonssikkerhet være en integrert del av måten vi tenker på utvikling og drift. \n\nFor å få til dette har vi, og mange andre, styrende dokumenter som setter krav til og beskriver utviklingsprossen - men dette i seg selv er ikke nok til å skape en god kultur for etterlevelse. For å gjøre vår guideline for sikker utvikling til en levende prosess som alle forsøker å etterleve, har vi satset stort på å integrere automatiserte tester i utviklingen, i tillegg til et høyt fokus på trusselmodellering og sikkerhetsbevissthet. I denne presentasjonen gir vi en introduksjon til vår tilnærming - og vil avslutte med noen tanker om fallgruver og suksessfaktorer for innføring av systematisk bruk av automatisert testing i sikkerhetssammenheng. Vi tar deg gjennom disse temaene for å vise tankeprosessen i praksis: \n\n- Trusselbildet i dag for webapplikasjoner med store brukermasser, og særlig innenfor sport og underholdning.\n- Hvorfor vi må ha sikkerhetsmetrikker, og når trenger vi dem? \n- Opplæring: hvorfor \"destruktiv programmering\" er en god tilnærming\n- Fitness for development: minstekrav for å starte et utviklingsløp\n- Integrasjon av automatisert statisk analyse, komponentanalyse og dynamisk testing i en typisk sprint\n- Hvilken rolle spiller mer dyptgående penetrasjonstesting i dette bildet? \n- KPI-er for kontinuerlig forbedring og strategisk oversikt\n- Oppsummering med fallgruver, suksessfaktorer og utfordringer man støter på\n","title":"Security in development: how automated security testing drives good practice","room":"Room 4","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T13:30:00Z","speakers":[{"twitter":"@sjefersuper","pictureId":"a21d947f8a2b489aa2ffcbb7471ea890","pictureUrl":"https://api.trondheimdc.no/public/picture/a21d947f8a2b489aa2ffcbb7471ea890","name":"Håkon Olsen","bio":"Håkon Olsen holds a master's degree in process-systems engineering from NTNU, where he graduated in 2006. He has been a research fellow with NTNU from 2006-2010, performing research on optimal operation under uncertainties using feedback control. In 2010 he joined Scandpower/Lloyd's Register where he worked as a consultant within risk management and with a particular focus on reliability and security of safety critical systems in oil and gas, shipping and railway. In 2017 he joined Sportradar as a security engineer and where he has worked extensively on application security and automation of security testing in the development process. "}],"startTime":"2018-10-22T15:30","endTime":"2018-10-22T16:00"},{"intendedAudience":"IoT enthusiasts!","endTimeZulu":"2018-10-22T14:45:00Z","level":"beginner","length":30,"format":"presentation","language":"en","sessionId":"a35db6b40f02414899a14463208c3564","abstract":"MicroPython is a Python implementation for embedded processors. It provides you with a subset of Python 3 features including numeric types, strings, tuples, lists, dictionaries and classes. Large parts of the Python standard library have also been ported.\n\nThis session will give an introduction to MicroPython, discuss how it differs from traditional CPython, and how to get up and running with coding yourself.","title":"MicroPython - The snake that fits in small places","room":"Room 5","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T14:15:00Z","speakers":[{"twitter":"","pictureId":"562b2e8eba1941b0b9cf5aa96c3939e3","pictureUrl":"https://api.trondheimdc.no/public/picture/562b2e8eba1941b0b9cf5aa96c3939e3","name":"Sebastian Roll","bio":"Sebastian is a chemical engineer turned software developer. Previously working with fire simulation and probabilistic risk analysis in the Oil & Gas sector, he has transitioned into software engineering and now works as a Webstep consultant. \n\nSebastian is passionate about improving quality and reproducibility in science and research by bridging the knowledge gap between scientists and software engineers."}],"startTime":"2018-10-22T16:15","endTime":"2018-10-22T16:45"},{"intendedAudience":"everyone","endTimeZulu":"2018-10-22T10:00:00Z","level":"beginner","length":"60","format":"presentation","language":"en","sessionId":"e28175abb38e4ad0aaa50c20fc894550","abstract":"Curious about Artificial Intelligence? Start here with Machine Learning — what it is, what it isn't, and how we all interact with it every day.\n\nJoin product developer and keynote speaker Christian Heilmann for a fascinating class all about Machine Learning. From how we all use it to where it's headed in the future, you'll learn the ins and outs of how machines are processing our data, finding patterns, and making our lives easier every day. With a focus on how machine learning can power human interfaces and ease our interactions with technology, lessons are packed with tools and tips for developers, designers, and the curious-minded.","title":"Artificial  intelligence  for  humans...","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T09:15:00Z","speakers":[{"twitter":"@codepo8","pictureId":"9b2439a5a70a4542b1ec4ea2be0c9b40","pictureUrl":"https://api.trondheimdc.no/public/picture/9b2439a5a70a4542b1ec4ea2be0c9b40","name":"Christian Heilmann","bio":"The robots are coming and we need to make a great example for them. Program Manager for Open Web and Browsers at Microsoft. Presenter, JavaScript lover."}],"startTime":"2018-10-22T11:15","endTime":"2018-10-22T12:00"},{"intendedAudience":"everyone","endTimeZulu":"2018-10-22T08:00:00Z","level":"beginner","length":"50","format":"presentation","language":"en","sessionId":"64d1b4ed81b948c5ac7ebabdad50e96c","abstract":"Technology has been good to us and it is still baffling just how useful it can be. It is also in a sorry state. The reason is a history of shortcuts and adding random things to reach a deadline that will bite us sooner than we think. Time to reconsider what we are doing and taking a more supporting look at the technology we use and build. ","title":"Watch this space! ","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T07:10:00Z","speakers":[{"twitter":"@codepo8","pictureUrl":"https://www.gravatar.com/avatar/07fcd228af02d476b1b8367d85a903b2?d=mm","name":"Chris Heilmann","bio":"The robots are coming and we need to make a great example for them. Program Manager for Open Web and Browsers at Microsoft. Presenter, JavaScript lover."}],"startTime":"2018-10-22T09:10","endTime":"2018-10-22T10:00"},{"intendedAudience":"This is a technical solution to a problem that is often seen as a user problem. I hope it can inspire other developers to also look for ways that tech could solve some things that at first glance seems like a user problem. In addition, what I’m using are relatively new technologies that haven’t been put to use as much as they deserve. By giving a short glimpse into these technologies, I believe more people would be inspired to try them out in their own projects.\n\nCode in slides","endTimeZulu":"2018-10-22T12:30:00Z","level":"beginner","length":"30","format":"presentation","language":"no","sessionId":"cb6355f82b4e4ab0a056882ad813eee3","abstract":"Nesten alle videosamtaler begynner med de samme kleine spørsmålene. Kan du høre meg? Hva med nå? Jeg ser deg, ser du meg? Hva om vi kunne unngått hele denne klønete måten å starte en videosamtale på? \n\nVed hjelp av WebRTC, WebAudio og Canvas vil jeg vise hvordan man kan bruke disse teknologiene til å lage et brukervennlig og tilgjengelig verktøy som gjør deg klar for videosamtaler - uten en 10 minutters debuggingsesjon.","title":"Hallo, kan du høre meg? Slutten på kleine videosamtaler takket være kode","room":"Room 2","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:00:00Z","speakers":[{"twitter":"@ingvildindrebo","pictureId":"b7c9c80ff2b64eeb96ef719f740ade33","pictureUrl":"https://api.trondheimdc.no/public/picture/b7c9c80ff2b64eeb96ef719f740ade33","name":"Ingvild Indrebø","bio":"Ingvild is a Senior Developer at the video conferencing startup Confrere. She fell in love with coding after realising code makes it possible for her to to do something good for not just one person at the time - but for everyone that might be using what she built. Before joining Confrere, she worked 4 years at BEKK Consulting, specializing in frontend. A fun fact: even after working in a video conferencing startup, she still finds video conversations slightly awkward. "}],"startTime":"2018-10-22T14:00","endTime":"2018-10-22T14:30"},{"intendedAudience":"People new to kubernetes who are interested in learning how it works and the benefits it brings.","endTimeZulu":"2018-10-22T13:45:00Z","level":"beginner","length":45,"format":"presentation","language":"en","sessionId":"63d3fb3a15d64270b3abbaaeb67b43cd","abstract":"With the rise of container technology, Cloud Native computing is the latest trend.  Kubernetes is the second largest open source project in terms of activity (after linux), and is rapidly becoming the defacto container runtime for cloud and on-premise workloads.\n\nThis talk will introduce the key concepts in Kubernetes, and show how to deploy, scale, upgrade and monitor application in a cloud native world, with real world examples.","title":"An Introduction to Kubernetes","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T13:00:00Z","speakers":[{"twitter":"@meekrosoft","pictureId":"693b78554eac42ea81b8c68df1b2c6b2","pictureUrl":"https://api.trondheimdc.no/public/picture/693b78554eac42ea81b8c68df1b2c6b2","name":"Mike Long","bio":"Mike is a CTO at Praqma, a Continuous Delivery and Devops consulting company based in Scandinavia. He has extensive experience delivering software in various cultures and industries. He is a trainer, and helps organize several community events and conferences, including CoDe Academy which teaches Continuous Delivery to university students. Mike is a trustee on the cyber-dojo foundation."}],"startTime":"2018-10-22T15:00","endTime":"2018-10-22T15:45"},{"intendedAudience":"This is intended for anyone who doesn't know much about Python but is interested in learning. At a minimum, participants will see how a Python program looks and what it's like to build a program in it. The presentation uses code extensively, with quite a bit of live coding. It does not, however, expect the audience to understand the coe; we use the coding experience as a way to introduce Python.","endTimeZulu":"2018-10-22T09:00:00Z","level":"beginner","length":45,"format":"presentation","language":"en","sessionId":"68feaee7405f4c47b53eb9a95276f671","abstract":"Since its first release more than 20 years ago, Python has gone from being an obscure language from the Netherlands to one of the most widely used programming languages in the world. Python combines a powerful, expressive language with an extensive standard library, a vast ecosystem of third-party packages, and an active open-source community. Its approachability means that almost anyone - from casual programmers to professional developers - can engage with the language at the level they need, creating everything from simple scripts to complex applications. \n\nIn this talk we’ll take a high-level tour of the language, and we’ll try to get a sense of how it feels to use Python. Rather than focus on the technical details of the language, we’ll go through actual Python programs to see how they work in practice. If you’re thinking of using Python, or are just curious, this talk will help you understand what Python can do and how it might work for you. ","title":"Introduction to Python","room":"Room 4","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T08:15:00Z","speakers":[{"twitter":"@austin_bingham","pictureUrl":"https://www.gravatar.com/avatar/b72b29740643f1e634d4aca1e6f079ab?d=mm","name":"Austin Bingham","bio":"Austin is a founding director of Sixty North, a software consulting, training, and application development company. A native of Texas, in 2008 Austin moved to Stavanger, Norway where he helped develop industry-leading oil reservoir modeling software in C++ and Python. Prior to that he worked at National Instruments developing LabVIEW, at Applied Research Labs (Univ. of Texas at Austin) developing sonar systems for the U.S. Navy, and at a number of telecommunications companies. He is an experienced presenter, teacher, and author, as well as an active member of the open source community. Austin is the founder of Stavanger Software Developers, one of the largest and most active social software groups in the Stavanger region. Austin holds a Master of Science in Computer Engineering from the University of Texas at Austin. "}],"startTime":"2018-10-22T10:15","endTime":"2018-10-22T11:00"},{"intendedAudience":"Du bør kunne grunnleggende programmering i C# (eller Java, det er nære nok). Ellers kreves det ingen forkunnskaper.\n\nDenne workshopen passer for alle som er nysgjerrig på hvordan man kommer igang med et prosjekt i Unity.","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","level":"beginner","speakers":[{"twitter":"@EirikWahl","pictureUrl":"https://www.gravatar.com/avatar/00494564e1940dba5819420bf1ebfc7f?d=mm","name":"Eirik Folstad Wahl","bio":"Eirik har jobbet med systemutvikling siden 2000. Han har vært konsulent i Itema i 7 år, hvor de tre siste årene har vært arbeid med Unity på prosjekt for Bertheussen IT på Work-Work, og nå på prosjekt i AR/VR-teamet på Equinor på Rotvoll. Eirik elsker (i tilfeldig rekkefølge) systemutvikling, Android, ungene, spillutvikling, kona, Java, C#, Unity, Android Studio, VR, AR, og å beskrive seg selv i tredjeperson.\n\nEirik har brukt Unity profesjonelt i ca 2-3 år: Han var med på å lage mobilspillet Race Day for Bertheussen IT, og lager nå applikasjoner for iPad, PC, HoloLens og VR-headset, og han har også laget Itemas TDC-hit \"VR-Skotthyll\" i Unity."}],"length":"240","format":"workshop","language":"no","sessionId":"609ceea89a5f45d48b397fafc58623d1","abstract":"Unity er verdens mest brukte spillmotor. Den har støtte for å lage spill og andre applikasjoner for \"alle\" plattformer - og enda noen til. Unity-kompetanse etterspørres i Trondheims-område for tiden, både for bilsimulator, spill, visualiseringsløsninger og annet.\n\nEirik Wahl fra Itema skal gi en grunnleggende innføring i hvordan man kommer igang med utvikling for Unity. Du bør kunne grunnleggende programmering i C# (eller Java, det er nære nok). Ellers kreves det ingen forkunnskaper.\n\nDet vil være en kort introduksjon hvor Unity presenteres, etterfulgt av en Workshop hvor vi i fellesskap går gjennom Unity sin egen \"Roll-a-ball tutorial\". Hvis det blir tid, så gjør vi det spillet litt mer kreativt.\n\nTa med egen PC (Windows eller MacOS) med forhåndsinstallert Unity (https://store.unity.com/download).","title":"Introduksjon til Unity"},{"intendedAudience":"This workshop is for object-oriented programmers curious about functional programming and the mathematical foundations of programming. You'll see F# and Haskell code examples during the workshop, but you don't have to know these language; you'll learn what you need along the way.","endTimeZulu":"2018-10-23T15:00:00Z","level":"beginner","length":"480","format":"workshop","language":"en","sessionId":"f136a2829fbb468590153e8717f180eb","abstract":"To attend either buy a TDC ticket with this workshop included for a discounted price or go to https://programutvikling.no/course/from-design-patterns-to-universal-abstractions/\n\nWould you like to write readable code? Code that you can understand a year later? Code that your co-workers can understand?\n\nIf so, you need to use good abstractions in your source code. What makes an abstraction good? How do you arrive at good abstractions?\n\nMost programmers try to 'invent' abstractions from scratch. This is an elusive goal. It requires a level of foresight rarely available. What if, instead, you could use existing, universal abstractions?\n\nFor decades, programmers have dreamt of being able to assemble software from building blocks, like Lego bricks. In order to do this, such building blocks must be composable, like Lego bricks. What makes an abstraction composable? It turns out that lambda calculus, abstract algebra, and category theory can teach us about composability and other fundamental abstractions of programming. Furthermore, it turns out that some of the most important design patterns in the Gang of Four book are special cases of such universal abstractions.\n\nFraming programming abstractions in mathematical terms has some advantages, one of which is that we get objective laws that we can use to test our assumptions. You can, for example, use such laws to determine whether an abstraction is composable – even in object-oriented programming!\n\nIn this workshop, you'll learn some objective criteria to determine whether a design is composable, and when various design patterns apply. No special background in mathematics is required.\n\nThe workshop will include a combination of lecture and exercises, including hands-on programming exercises.\n\nSuccessful participation in the workshop should enable you to use universal abstractions in your source code. As always, no silver bullet is implied. You will learn about universal abstractions, but it will not magically make all your code instantly perfect.","title":"From Design Patterns to universal abstractions","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-23T07:00:00Z","speakers":[{"twitter":"@ploeh","pictureUrl":"https://www.gravatar.com/avatar/33526616d4a11f1630d4939b769cfbf9?d=mm","name":"Mark Seeman","bio":"Mark Seemann helps programmers make code easier to maintain. His professional interests include functional programming, object–oriented development, software architecture, as well as software development in general. Apart from writing a book about Dependency Injection he has also created several Pluralsight courses, and written numerous articles and blog posts about programming. Originally poised to become a rock star or (failing that) graphic novelist (in the European tradition) he one day found himself with insufficient talent for either, a master's degree in Economics, and a desire for working with computers. He has been doing the latter intermittently since 1995. When not working with software or spending time with his family, Mark enjoys reading, listening to and playing music, as well as preparing or consuming gourmet food and wine."}],"startTime":"2018-10-23T09:00","endTime":"2018-10-23T17:00"},{"intendedAudience":".NET developers, or developers who are interested in learning about .NET. We'll have a mix of slides and live coding.","endTimeZulu":"2018-10-22T09:00:00Z","level":"beginner","length":"45","format":"presentation","language":"en","sessionId":"b66933dfa5a144099450345e58eccaf2","abstract":"Learn about the present and the future of the .NET Platform. We'll take a look at the current state of the .NET ecosystem, where we're going next, and demo some top features in the newest and upcoming .NET Core releases. We'll also show you the exciting direction .NET is headed with .NET Core 3 to help you build intelligent, cutting edge solutions for the future no matter what operating system you are targeting.","title":".NET Today and Tomorrow","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T08:15:00Z","speakers":[{"twitter":"@jongalloway","pictureId":"bce4b9ecd181494b9564a26c9a29e484","pictureUrl":"https://api.trondheimdc.no/public/picture/bce4b9ecd181494b9564a26c9a29e484","name":"Jon Galloway","bio":"Jon is the Executive Director of the .NET Foundation and a Program Manager on the .NET team. He’s been developing software professionally since the late 1990’s, and over the years has written several books, popular .NET tutorials and samples, a top speaker on Microsoft Virtual Academy, and worked behind the scenes on large Microsoft keynote demonstrations. He’s helped run the Herding Code podcast for 10 years, and tweets as @jongalloway."}],"startTime":"2018-10-22T10:15","endTime":"2018-10-22T11:00"},{"intendedAudience":".NET developers","endTimeZulu":"2018-10-22T10:00:00Z","level":"beginner","length":"45","format":"presentation","language":"en","sessionId":"41e6b1d15be74e6dbfb726d91d2ac86a","abstract":"Ready to move from .NET Framework to .NET Core? There are a lot of great benefits to the move, but some challenges as well. We'll review the guidance on what to migrate and what to leave as-is, take a look at some important tools, and dig into how .NET Core 3.0 and .NET Standard fit in. We'll talk about some top strategies for a successful migration, cover some important testing strategies to make sure we don't break things along the way, and look at some examples to apply what we've learned throughout the session. Let's do this!","title":"From .NET Framework To .NET Core","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T09:15:00Z","speakers":[{"twitter":"@jongalloway","pictureId":"252c892d9a2e471fb4b803413e56caa2","pictureUrl":"https://api.trondheimdc.no/public/picture/252c892d9a2e471fb4b803413e56caa2","name":"Jon Galloway","bio":"Jon is the Executive Director of the .NET Foundation and a Program Manager on the .NET team. He’s been developing software professionally since the late 1990’s, and over the years has written several books, popular .NET tutorials and samples, a top speaker on Microsoft Virtual Academy, and worked behind the scenes on large Microsoft keynote demonstrations. He’s helped run the Herding Code podcast for 10 years, and tweets as @jongalloway."}],"startTime":"2018-10-22T11:15","endTime":"2018-10-22T12:00"},{"intendedAudience":"Most features are easy to understand for developers who worked with Java for at least three years. Placing them in a larger context sometimes requires a little more experience. Features are independent from one another and hence can be studied one at a time.","endTimeZulu":"2018-10-23T16:00:00Z","level":"intermediate","length":"480","format":"workshop","language":"en","sessionId":"2c98ae61c0214cad8fb2c7ef1a894620","abstract":"We had a long time to get used to lambdas and streams, but since Java 9 was released in September 2017, features are coming fast:\n\n* language changes like private interface methods, diamond operator for anonymous classes and local-variable type inference with `var`\n* new and improved APIs like collection factories, stack walking, and a reactive HTTP client\n* JVM additions like multi-release JARs, application class-data sharing, and general performance improvements\n\nAll of these and more features were added just in the recent year.\n\nThis workshop gives you an overview over the changes to the Java language and the JVM after Java 8 and lets you get your hands dirty with the most important and interesting ones.","title":"Java After Eight","room":"DIGS","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-23T08:00:00Z","speakers":[{"twitter":"@nipafx","pictureUrl":"https://www.gravatar.com/avatar/13eee0ac5d38daea3d6ca6aa6efea5b3?d=mm","name":"Nicolai Parlog","bio":"Nicolai is a thirty year old boy, as the narrator would put it (if he squints), who has found his passion in software development. He constantly reads, thinks, and writes about it, and codes for a living as well as for fun.\n\nNicolai is a freelancing developer, author, and trainer. He wrote The Java Module System[1] with Manning, blogs about software development on codefx.org[2], recently started a YouTube channel[3], and is a long-tail contributor to several open source projects. You can hire him[4] for all kinds of things.\n\n[1]: https://www.manning.com/books/the-java-module-system?a_aid=nipa&a_bid=869915cb\n[2]: http://codefx.org\n[3]: https://youtube.com/c/codefx\n[4]: http://blog.codefx.org/hire-nicolai-parlog/\n"}],"startTime":"2018-10-23T10:00","endTime":"2018-10-23T18:00"},{"intendedAudience":"Some code, some tools, maybe a demo. Developers and Senior Developers who want to refresh their ideas should attend.\n","endTimeZulu":"2018-10-22T12:45:00Z","level":"intermediate","length":"45","format":"presentation","language":"en","sessionId":"01165eca3d3e423383b486cb569087d5","abstract":"JavaScript can be daunting. It is a dozen different things to different people and all of them have the one true way to use it or you are not \"professional\". In this talk let's shine a calming light on the situation and leave you with five (or more) things to do to make you a happy and productive JavaScript developer. Not a burned out ninja rockstar super unicorn hell-bent on making their mark on hacker news for the day.","title":"Five things to make you a happier JavaScript developer","room":"Room 1","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:00:00Z","speakers":[{"twitter":"@codepo8","pictureUrl":"https://www.gravatar.com/avatar/07fcd228af02d476b1b8367d85a903b2?d=mm","name":"Christian Heilmann","bio":"The robots are coming and we need to make a great example for them. Program Manager for Open Web and Browsers at Microsoft. Presenter, JavaScript lover."}],"startTime":"2018-10-22T14:00","endTime":"2018-10-22T14:45"},{"intendedAudience":"Some live demos","endTimeZulu":"2018-10-22T14:30:00Z","level":"intermediate","length":30,"format":"presentation","language":"en","sessionId":"10ea5d0ead5543c4ac8f5937396f084b","abstract":"With the emerging popularity of bug bounty programs, lesser known and even brand new vulnerability classes are gaining popularity.\n\nThis talk will give a walk-though of some of these vulnerabilities, how they occur in modern web applications and how they can be found and fixed.","title":"Modern web application security bugs","room":"Room 2","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T14:00:00Z","speakers":[{"twitter":"@webtonull","pictureId":"8b0c613851f9423a9beb5d59a3ce696a","pictureUrl":"https://api.trondheimdc.no/public/picture/8b0c613851f9423a9beb5d59a3ce696a","name":"Erlend Oftedal","bio":"Erlend has worked as a developer and security consultant for over 14 years, trying to build and break many different types of systems. He spends some of his free time on security research and open source tools, and is the main author behind retire.js - a free and open source scanner for JavaScript. He is also the chapter leader of the Norwegian OWASP chapter."}],"startTime":"2018-10-22T16:00","endTime":"2018-10-22T16:30"},{"intendedAudience":"everyone","endTimeZulu":"2018-10-22T09:15:00Z","level":"beginner","length":45,"format":"presentation","language":"en","sessionId":"2ce5b99d09724fbba64b85e8fd552489","abstract":"How did we get here?  This talk will provide a new view of the Agile and DevOps movements, through the lens of scientific and cognitive revolutions.\n\nYou will learn how to make strategic choices in recruitment, organization, culture, and technology to best situate an organization for success in this brave new world.","title":"The Structure of Software Revolutions","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T08:15:00Z","speakers":[{"twitter":"@meekrosoft","pictureId":"3a8a921d63a94cc0b8a6adfd66be66cb","pictureUrl":"https://api.trondheimdc.no/public/picture/3a8a921d63a94cc0b8a6adfd66be66cb","name":"Mike Long","bio":"Mike is a CTO at Praqma, a Continuous Delivery and Devops consulting company based in Scandinavia. He has extensive experience delivering software in various cultures and industries. He is a trainer, and helps organize several community events and conferences, including CoDe Academy which teaches Continuous Delivery to university students. Mike is a trustee on the cyber-dojo foundation."}],"startTime":"2018-10-22T10:15","endTime":"2018-10-22T11:15"},{"intendedAudience":"This presentation is suitable for all developers interested in how they can expand their toolbox with web assembly and be able to make a sound assessment of when and how it should be used. ","endTimeZulu":"2018-10-22T12:30:00Z","level":"intermediate","length":30,"format":"presentation","language":"en","sessionId":"6949ed73455b4cc2b7451163ecb27902","abstract":"During the last decades a growing trend has been to put more and more functionality into the client by using the latest and greatest JavaScript framework. But what if we could be using native code in the browser in order to run computations faster and potentially reuse code from the backend in the frontend. \n\nEnter Web Assembly. Web assembly is a new web standard which enables you to run native code as part of your current JavaScript framework. This talk will give you a thorough understanding of what web assembly is and how you can use it in your project. \n\nWe will cover a practical example writing our web assembly using Rust. We will go through everything from writing your web assembly code to publish it as a npm package and finally use it in an existing web application.\n\nThis presentation is suitable for all developers interested in how they can expand their toolbox with web assembly and be able to make a sound assessment of when and how it should be used.\n","title":"Why you should consider Web Assembly in your next frontend project ","room":"Room 3","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:00:00Z","speakers":[{"twitter":"@agrevlis","pictureId":"051cae5dee8b42ac922a4b0449d60851","pictureUrl":"https://api.trondheimdc.no/public/picture/051cae5dee8b42ac922a4b0449d60851","name":"Håkan Silfvernagel","bio":"Håkan holds a Master of Science degree in Electrical Engineering and in addition, he holds a Master’s degree in Behavioral Science. He is currently finishing a Master's degree in Emotional Psychology. He has previously worked as a researcher within the field of human computer interaction in the process industry with a focus on process automation and robotics. \n\nHe has 19 years’ experience of software development in various positions such as developer, tester, architect, project manager, scrum master, practice manager and team lead.\n\nHis main interests lies in how technology and psychology can be combined in order to develop system and services that are more adapted to how we as humans functions.\n\nHe is a board member for the Norwegian .NET User Group (NNUG) Oslo."}],"startTime":"2018-10-22T14:00","endTime":"2018-10-22T14:30"},{"intendedAudience":"I dette foredraget vil alle og en hver finne noe de kan ta med seg. Om det er generell CSS layouthistorie, historien bak CSS Grid, eller konkret inspirasjon fra spennende kodeeksempler. Foredraget har både kode i slides og inneholder live koding. Live-kodingen er forsøkt gjort underholdende, for å appellere til både teknologer uten kodeerfaring og erfarne utviklere.","endTimeZulu":"2018-10-22T13:15:00Z","level":"beginner","length":30,"format":"presentation","language":"no","sessionId":"c085fd69e0cd41f7a9ab1d6141507c52","abstract":"CSS er noe de fleste frontendutviklere er nødt til å forholde seg til, og CSS er for mange det som havner bakerst i prioriteringskøen når man skal oppsøke og tilegne seg ny kunnskap. Nå som CSS Grid er på veg inn i standarden, er det derfor på tide å børste støv av CSS-entusiasmen.\n\nDette foredraget vil være en introduksjon til CSS Grid og vil ta for seg det grunnleggende du trenger for å kunne ta i bruk CSS Grid i hverdagen. \n\nForbered deg på et interaktivt foredrag med live demoer, som vil forsøke å inspirere akkurat nok til at du selv oppsøker mer avansert informasjon om CSS Grid etterpå. Foredraget forutsetter generell CSS-erfaring, men har man ikke rørt CSS på evigheter, vil dette oppleves som å sette seg i en tidsmaskin.","title":"Hva er greia med CSS Grid?","room":"Room 5","conferenceId":"30d5c2f1cb214fc8b0649a44fdf3b4bf","startTimeZulu":"2018-10-22T12:45:00Z","speakers":[{"twitter":"@krakels","pictureUrl":"https://www.gravatar.com/avatar/fd5606a96714d8a21d010621dea143e6?d=mm","name":"Marius Krakeli","bio":"Marius er en programmerer med hjerte for fantastiske brukeropplevelser. Han er fascinert av JavaScript og er både produsent og panelist på JavaScript podcasten “BartJS Podcast”. Når han trenger en pause fra programmering, er han veldig underholdt av dataspill og videoproduksjon. Han har den siste tiden brukt mye tid på å finne ut hvorfor CSS Grid kommer til å revolusjonere måten vi gjør layout på web, og vil formidle det han har funnet igjennom historiefortelling og engasjerende livekoding."}],"startTime":"2018-10-22T14:45","endTime":"2018-10-22T15:15"}]};

export default function getProgram() {
    return new Promise((resolve, reject) => {
        resolve(program)
    });
}